export default "<template>\n  <div>\n    <div class=\"section\">\n      <h1>Step</h1>\n      <p>step component</p>\n\n      <pre><code>import { Step } from 'streamlabs-beaker';\n\ncomponents: {\n  Step\n}</code></pre>\n    </div>\n\n    <div class=\"section\">\n      <div class=\"row\">\n        <DemoSection title=\"Default\" :code=\"demoCode\">\n          <template #components>\n            <FormGroup>\n              <Step slot=\"input\" title=\"Enable Pro\">\n                <div class=\"info\">\n                  <p>+20 CCV</p>\n                  <i class=\"icon-information\"></i>\n                </div>\n              </Step>\n\n              <Step\n                slot=\"input\"\n                title=\"Enable Pro\"\n                :isCompleted=\"true\"\n                :hasCheckmark=\"true\"\n                completedText=\"Complete\"\n              >\n                <div class=\"info\">\n                  <p>+20 CCV</p>\n                  <i class=\"icon-information\"></i>\n                </div>\n              </Step>\n\n              <Step\n                slot=\"input\"\n                icon=\"icon-donation-settings\"\n                title=\"Tip\"\n                :hasPrime=\"true\"\n                >0/1</Step\n              >\n\n              <Step\n                slot=\"input\"\n                icon=\"icon-donation-settings\"\n                title=\"Tip\"\n                :isCompleted=\"true\"\n                completedText=\"1/1\"\n                >0/1</Step\n              >\n            </FormGroup>\n          </template>\n        </DemoSection>\n      </div>\n\n      <table class=\"docs-table\">\n        <thead>\n          <tr>\n            <th>Props</th>\n            <th>Type</th>\n            <th>Default</th>\n            <th>Description</th>\n          </tr>\n        </thead>\n        <tbody>\n          <tr>\n            <td>title</td>\n            <td>String</td>\n            <td>null</td>\n            <td>title to display</td>\n          </tr>\n          <tr>\n            <td>icon</td>\n            <td>String</td>\n            <td>null</td>\n            <td>icon to display</td>\n          </tr>\n          <tr>\n            <td>isCompleted</td>\n            <td>Boolean</td>\n            <td>false</td>\n            <td>\n              background would be @dark-4, color would be @dark-5 when it is\n              true\n            </td>\n          </tr>\n          <tr>\n            <td>completedText</td>\n            <td>String</td>\n            <td>null</td>\n            <td>\n              It shows instead of slot when isCompleted is true\n            </td>\n          </tr>\n          <tr>\n            <td>isChecked</td>\n            <td>Boolean</td>\n            <td>false</td>\n            <td>\n              add check mark icon and line through for title when it is true\n            </td>\n          </tr>\n          <tr>\n            <td>hasPrime</td>\n            <td>Boolean</td>\n            <td>false</td>\n            <td>It shows prime alt badge when it is true</td>\n          </tr>\n        </tbody>\n      </table>\n    </div>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport { Component, Vue } from \"vue-property-decorator\";\n\nimport DemoSection from \"./../components/DemoSection.vue\";\nimport FormGroup from \"./../components/FormGroup.vue\";\nimport Step from \"./../components/Step.vue\";\nimport StepsCode from \"./Steps.vue?raw\";\n\n@Component({\n  components: {\n    DemoSection,\n    FormGroup,\n    Step\n  }\n})\nexport default class Steps extends Vue {\n  demoCode = StepsCode;\n}\n</script>\n\n<style lang=\"less\" scoped>\n@import (reference) \"./../styles/Imports\";\n\n.info {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n\n  p {\n    .margin(0);\n    margin-right: 15px;\n    color: @dark-5;\n    background: @light-blue;\n    .weight(@medium);\n    .radius();\n    .padding();\n  }\n}\n</style>\n"